* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

// body {
//   background-color: red;
// }

.nav-list {
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.nav-links {
  text-decoration: none;
  color: rgb(185, 92, 92);
  font-size: 18px;
}

.home, 
.about {
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home {
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

.home-content,
.about-content {
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  
  img {
    height: 70%;
  }

  h1 {
    font-size: 50px;
    margin: 30px 0;
  }

  p {
    font-size: 20px; 
  }
}